import gql from 'graphql-tag'
import { apolloClient } from '@/store/apollo'
import { getPublisherById, newPublisher } from '@/gql/publisher'

const state = {
    publisher: null
}
const getters = {
  // ...
}
const mutations = {
  setPublisher: (state, publisher) => {
    state.publisher = publisher
  },
}
const actions = {
  async getPublisherById({ commit }, payload) {
    const { data } = await apolloClient.query({
      query: getPublisherById
    })

    commit('setPublisher', data)
  },

  async addPublisher({}, payload) {
    const { data } = await apolloClient.mutate({
      mutation: newPublisher,
      variables: {
        label: this.newTag,
      },
    })
  },
}

// Always namespace your vuex modules.
export const main = {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}



